// app/dashboard/send-money/page.tsx
'use client';

import { useEffect, useState } from 'react';
import { apiFetch, isAuthenticated } from '@/src/lib/api/api-client';
import { useAuth } from '@/src/context/AuthContext';

export default function SendMoneyPage() {
  const { user } = useAuth();
  const [form, setForm] = useState({ receiver_id: '', amount: '', currency: 'MWK', description: '', channel: 'web', category: 'transfer' });
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const [result, setResult] = useState<any | null>(null);

  useEffect(() => {
    const cc = String(user?.countryCode || '').toUpperCase();
    if (cc === 'CN') setForm((f) => ({ ...f, currency: 'CNY' }));
    else if (cc === 'MW') setForm((f) => ({ ...f, currency: 'MWK' }));
  }, [user?.countryCode]);

  const onChange = (e: React.ChangeEvent<HTMLInputElement | HTMLSelectElement>) => {
    const { name, value } = e.target;
    setForm((f) => ({ ...f, [name]: value }));
  };

  const onSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setLoading(true);
    setError(null);
    setResult(null);
    try {
      if (!isAuthenticated()) throw new Error('Please login to send a payment');
      const payload = {
        receiver_id: form.receiver_id,
        amount: Number(form.amount),
        currency: form.currency,
        description: form.description,
        channel: form.channel,
        category: form.category,
      };
      const res = await apiFetch('/payments/initiate', { method: 'POST', body: JSON.stringify(payload) });
      setResult(res);
    } catch (e: any) {
      setError(e?.data?.error || e?.message || 'Payment failed');
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="space-y-6">
      <h1 className="text-2xl font-semibold">Send Money</h1>
      <p className="text-muted-foreground">Initiate cross-border transfers and payouts here.</p>

      <form onSubmit={onSubmit} className="bg-card text-card-foreground rounded-xl border p-6 space-y-4 max-w-xl">
        <div>
          <label className="text-sm text-muted-foreground">Receiver ID (UUID)</label>
          <input name="receiver_id" value={form.receiver_id} onChange={onChange} className="mt-1 w-full border rounded-md px-3 py-2 bg-background" placeholder="e.g. user UUID" required />
        </div>
        <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
          <div>
            <label className="text-sm text-muted-foreground">Amount</label>
            <input name="amount" value={form.amount} onChange={onChange} type="number" min="0" step="0.01" className="mt-1 w-full border rounded-md px-3 py-2 bg-background" placeholder="0.00" required />
          </div>
          <div>
            <label className="text-sm text-muted-foreground">Currency</label>
            <select name="currency" value={form.currency} onChange={onChange} className="mt-1 w-full border rounded-md px-3 py-2 bg-background">
              {String(user?.countryCode || '').toUpperCase() === 'CN' ? (
                <>
                  <option value="CNY">CNY</option>
                </>
              ) : (
                <>
                  <option value="MWK">MWK</option>
                </>
              )}
            </select>
          </div>
        </div>
        <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
          <div>
            <label className="text-sm text-muted-foreground">Channel</label>
            <select name="channel" value={form.channel} onChange={onChange} className="mt-1 w-full border rounded-md px-3 py-2 bg-background">
              <option value="web">Web</option>
              <option value="mobile">Mobile</option>
            </select>
          </div>
          <div>
            <label className="text-sm text-muted-foreground">Category</label>
            <input name="category" value={form.category} onChange={onChange} className="mt-1 w-full border rounded-md px-3 py-2 bg-background" placeholder="e.g. transfer" />
          </div>
        </div>
        <div>
          <label className="text-sm text-muted-foreground">Description</label>
          <input name="description" value={form.description} onChange={onChange} className="mt-1 w-full border rounded-md px-3 py-2 bg-background" placeholder="Payment description" />
        </div>

        <button type="submit" disabled={loading} className="bg-green-600 hover:bg-green-700 text-white px-4 py-2 rounded-md">
          {loading ? 'Processing...' : 'Send Payment'}
        </button>
      </form>

      {error && (
        <div className="bg-red-50 dark:bg-red-900/20 text-red-700 dark:text-red-300 border border-red-200 dark:border-red-800 rounded-xl p-4 max-w-xl">{error}</div>
      )}

      {result?.transaction && (
        <div className="bg-card text-card-foreground rounded-xl border p-6 max-w-xl">
          <h2 className="text-xl font-semibold mb-4">Payment Result</h2>
          <div className="space-y-2 text-sm">
            <div className="flex justify-between"><span className="text-muted-foreground">Reference</span><span className="font-mono">{String(result.transaction.reference)}</span></div>
            <div className="flex justify-between"><span className="text-muted-foreground">Status</span><span className="font-semibold">{String(result.transaction.status)}</span></div>
            <div className="flex justify-between"><span className="text-muted-foreground">Amount</span><span className="font-semibold">{String(result.transaction.amount)}</span></div>
            <div className="flex justify-between"><span className="text-muted-foreground">Currency</span><span>{String(result.transaction.currency)}</span></div>
            <div className="flex justify-between"><span className="text-muted-foreground">Converted</span><span>{String(result.transaction.converted_amount)} {String(result.transaction.converted_currency)}</span></div>
          </div>
        </div>
      )}
    </div>
  );
}